// Third Party Imports
import React from 'react';
import {
	noop,
	map,
	filter,
	flow,
	omit,
	extend,
	isEmpty,
	isFunction,
	size
} from 'lodash';

// Internal Function Imports
import {
	getSelectedItems,
	isEditable,
	generateSubComponentIds,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

// Component Imports
import { OptionsList, Dropdown } from '@devsnc/sn-options-list';

const toValueObject = str => ({ value: str, displayValue: str });

/**
 * Adds state management and autocomplete to the basic Input component.
 */
export default Component =>
	withValueChangeCallbacks(
		class Autocomplete extends React.Component {
			static defaultProps = {
				onValueChange: noop
			};

			static actionHandlers = Component.actionHandlers;

			/**
			 * constructor
			 * @param {object} props
			 */
			constructor(props) {
				super(props);

				/**
				 * local state of the input for highlighting and user interaction
				 *
				 * @param {string} initialValue - initial value for the component (duh)
				 * @param {bool} isActive - toggle if the options are active
				 * @param {array} options - list of avalable options
				 * @param {number} selectedSearchResult - selected option index
				 */
				this.state = {
					isActive: false,
					options: props.autocompleteOptions || [],
					selectedSearchResult: -1
				};
			}

			supportsDropdown = () => {
				const { autocompleteOptions } = this.props;
				return !isEmpty(autocompleteOptions) && isEditable(this.props);
			};

			/**
			 * function that filters out the autoComplete options by the value user has entered
			 * @param {array} autocompleteOptions list of options
			 * @param {bool} autocompleteMatchAnywhere qualifier for type of search
			 * @returns {array} filtered list of options
			 */
			filterStaticOptions = (
				autocompleteOptions,
				autocompleteMatchAnywhere,
				value = ''
			) => {
				const matcher = autocompleteMatchAnywhere ? 'includes' : 'startsWith';
				const lowerCaseInput = value.toLowerCase();
				return autocompleteOptions
					? filter(autocompleteOptions, opt =>
							opt.toLowerCase()[matcher](lowerCaseInput)
					  )
					: this.state.options;
			};

			/**
			 * function pushes a new value and displayValue
			 * @param {string} value
			 * @param {string} displayValue
			 */
			closeDropdown = () => {
				this.setState({
					isActive: false
				});
			};

			/**
			 * callback for keyboard events that interact with the dropdown
			 * @param {object} event onKeyDown event object
			 */
			onKeyDown = event => {
				let { key } = event;
				let { options, selectedSearchResult } = this.state;
				switch (key) {
					case 'ArrowUp':
					case 'ArrowDown':
						event.preventDefault();
						this.setState({
							selectedSearchResult: getSelectedItems(
								key,
								false,
								undefined,
								[selectedSearchResult],
								size(options)
							)[0]
						});
						break;
					case 'Escape':
						this.setState({ selectedSearchResult: -1, isActive: false });
						break;
				}

				if (isFunction(this.props.onKeyDown)) {
					this.props.onKeyDown(event);
				}
			};

			/**
			 * callback to add a new value based on keyboard enter
			 * @param {object} event onKeyUp event object
			 */
			onKeyUp = event => {
				let { key } = event;
				if (key === 'Enter') {
					const { options, selectedSearchResult, isActive } = this.state;
					if (isActive) {
						const item = options[selectedSearchResult];
						if (item) this.selectOption(item);
					} else {
						this.setState({ isActive: true });
					}
				}

				if (isFunction(this.props.onKeyUp)) {
					this.props.onKeyUp(event);
				}
			};

			/**
			 * onChange callback passed to the input to indicate when a user has entered a value and we potentially
			 * want to show autocomplete options
			 * @param {object} event onChange event object
			 */
			onChange = event => {
				const value = event.target.value;
				const { autocompleteOptions, autocompleteMatchAnywhere } = this.props;

				let newState = {
					isActive: !isEmpty(value),
					selectedSearchResult: -1
				};
				if (
					autocompleteOptions &&
					Array.isArray(autocompleteOptions) &&
					autocompleteOptions.length > 0
				) {
					newState.options = this.filterStaticOptions(
						autocompleteOptions,
						autocompleteMatchAnywhere,
						value
					);
					newState.isActive = newState.isActive && newState.options.length > 0;
				}
				this.setState(newState);

				if (isFunction(this.props.onChange)) this.props.onChange(event);
			};

			/**
			 * callback thats passed to input to indicate blur state
			 * @param {object} event onBlur event object
			 */
			onBlur = event => {
				this.closeDropdown();

				if (isFunction(this.props.onBlur)) {
					this.props.onBlur(event);
				}
			};

			innerRef = node => {
				this.inputElement = node;

				if (isFunction(this.props.innerRef)) {
					this.props.innerRef(node);
				}
			};

			/**
			 * callback to set a hovered element as selected
			 * @param {number} optionIndex index of the hovered option
			 */
			onHoverOption = optionIndex => {
				this.setState({ selectedSearchResult: optionIndex });
			};

			/**
			 * callback thats passed to OptionsList to set a new value based on the option clicked
			 * @param {string} value index of a pill to be removed
			 */
			selectOption = value => {
				this.props.onValueChange({ value });
				this.closeDropdown();
				this.inputElement.focus();
			};

			/**
			 * update local state based off new prop rules
			 */
			componentWillReceiveProps = newProps => {
				const {
					autocompleteOptions,
					autocompleteMatchAnywhere,
					value
				} = newProps;
				let newState = {};

				if (
					autocompleteOptions !== this.props.autocompleteOptions ||
					autocompleteMatchAnywhere !== this.props.autocompleteMatchAnywhere
				) {
					newState.options = this.filterStaticOptions(
						autocompleteOptions,
						autocompleteMatchAnywhere,
						value
					);
				}

				this.setState(newState);
			};

			/**
			 * function to filter passed in props and convert them to HTML input attributes
			 * @return {object} input html attributes
			 */
			getAttributes = () => {
				return flow(
					props =>
						omit(props, [
							'autocomplete',
							'autocompleteOptions',
							'autocompleteMatchAnywhere'
						]),
					props => {
						if (!this.supportsDropdown()) {
							return props;
						}

						const dropdown = this.getDropdown();
						const ids = generateSubComponentIds(this.props.componentId, {
							options: size(this.props.options)
						});
						const activeDescendant = dropdown
							? ids.options[this.state.selectedSearchResult]
							: undefined;

						return extend(props, {
							dropdown,
							onBlur: this.onBlur,
							onChange: this.onChange,
							onKeyDown: this.onKeyDown,
							onKeyUp: this.onKeyUp,
							innerRef: this.innerRef,
							'aria-owns': dropdown && ids.dropdown,
							'aria-haspopup': 'listbox',
							'aria-autocomplete': 'list',
							'aria-expanded': this.state.isActive,
							'aria-activedescendant': activeDescendant
						});
					}
				)(this.props);
			};

			/**
			 * assemble the dropdown markup based on passed in props
			 * @return {ReactElement} markup
			 */
			getDropdown = () => {
				let { componentId } = this.props;
				let { isActive, options, selectedSearchResult } = this.state;
				if (isActive) {
					return (
						<Dropdown>
							<OptionsList
								parentId={componentId}
								options={map(options, toValueObject)}
								selectedSearchResults={[selectedSearchResult]}
								onClick={this.selectOption}
								onHover={this.onHoverOption}
							/>
						</Dropdown>
					);
				}

				return null;
			};

			/**
			 * render
			 * @return {ReactElement} markup
			 */
			render() {
				return <Component {...this.getAttributes()} />;
			}
		}
	);
