import React from 'react';
import MatchListItem from './MatchListItem';
import { getScrollParent } from '@devsnc/sn-controls-common';

import { t } from 'sn-translate';
import { isEmpty } from 'lodash';
import makeClass from 'classnames';

// Number of pixels to maintain as horizontal margin between the matchlist and
// its scroll parent.
const OVERFLOW_MARGIN = 40;

export default class MatchList extends React.PureComponent {
	static defaultProps = {
		dropdownLeft: 0,
		dropdownTop: 0
	};

	componentDidUpdate() {
		if (!this.listElement || !this.highlightedComponent) {
			return;
		}
		const childElementEdges = this.highlightedComponent.getElementEdges();
		const scrollTop = this.listElement.scrollTop;
		const scrollBottom = scrollTop + this.listElement.offsetHeight;
		if (
			childElementEdges.topEdge < scrollTop ||
			childElementEdges.bottomEdge > scrollBottom
		) {
			this.listElement.scrollTop = childElementEdges.topEdge;
		}
	}

	/**
	 * Reposition the left coord to fit within the visible window. Don't need to
	 * worry about vertical positioning because that's handled by Input dropdown
	 */
	componentDidMount() {
		if (!this.element) {
			return;
		}

		const scrollParent =
			getScrollParent(this.element) || this.element.offsetParent;
		const parentWidth = scrollParent.getBoundingClientRect().width;
		const matchListRect = this.element.getBoundingClientRect();

		const overflow =
			this.props.dropdownLeft +
			matchListRect.width +
			OVERFLOW_MARGIN -
			parentWidth;

		const realLeft =
			overflow > 0 && parentWidth > matchListRect.width
				? this.props.dropdownLeft - overflow
				: this.props.dropdownLeft;

		this.element.style.left = realLeft + 'px';
		this.element.style.visibility = 'visible';
	}

	render() {
		const { isNewDropdown, data, componentId, isLoading } = this.props;
		const message = isNewDropdown
			? t('Enter the name of a person you want to mention')
			: isLoading
			? t('Loading')
			: isEmpty(data)
			? t('No matches')
			: null;
		const className = makeClass('match-list', 'sn-dropdown', {
			'with-message': message
		});

		// Avoid FOUC: render hidden by default but become visible once we can
		// calculate the correct coordinates in componentDidMount
		return (
			<div
				ref={element => (this.element = element)}
				className={className}
				style={{
					visibility: 'hidden',
					top: this.props.dropdownTop + 'px'
				}}
			>
				{message && <p>{message}</p>}

				{!isEmpty(data) && !isNewDropdown && !isLoading && (
					<React.Fragment>
						<ul
							className="sn-list-group"
							ref={this._setListElement}
							aria-controls={componentId + '-list'}
						>
							{this._renderMatchListItems()}
						</ul>
						{this._renderARIARegion()}
					</React.Fragment>
				)}
			</div>
		);
	}

	_renderMatchListItems() {
		const { data, highlightedIndex, onItemSelected, componentId } = this.props;
		return data.map((item, i) => {
			const highlighted = i === highlightedIndex;
			let props = {
				...item,
				onItemSelected,
				highlighted,
				componentId,
				onHoverItem: () => this.props.onHoverItem(i)
			};
			if (highlighted) {
				props.ref = this._setHighlightedComponent;
			}
			return <MatchListItem key={item.id} {...props} />;
		});
	}

	_renderARIARegion() {
		const { data, highlightedIndex, componentId } = this.props;
		const highlighted = data[highlightedIndex];
		return (
			<div
				id={componentId + '-list'}
				aria-live="polite"
				className="now-a11y-label"
			>
				{highlighted.value}, {highlighted.secondary}
			</div>
		);
	}

	_setHighlightedComponent = component => {
		this.highlightedComponent = component;
	};

	_setListElement = el => {
		this.listElement = el;
	};
}
