// Third Party Imports
import React from 'react';
import { flow, omit, extend, toString } from 'lodash';
import merge from './merge';

import { withValueChangeCallbacks } from '@devsnc/sn-controls-common';

const passthrough = s => ({ value: s });

/**
 * Adds state management and the format/parse loop for inputs
 *
 * @argument Component the input component to wrap with state + formatting management
 * @argument Formatter with the shape of { parse, format }. Defaults to passthroughs
 */
export default (
	Component,
	{ parse = passthrough, format = passthrough, stopBubbling = [] } = {}
) => {
	const parseAndFormat = (value, props, additionalInfo = {}) => {
		const { value: parsedValue, error } = parse(value, props, additionalInfo);
		if (!error) {
			const { value: formattedValue, error } = format(
				parsedValue,
				props,
				additionalInfo
			);

			return { parsedValue, formattedValue, additionalInfo, error };
		}

		return { parsedValue, formattedValue: value, additionalInfo, error };
	};

	return withValueChangeCallbacks(
		{ initialValueProp: 'initialValue', stopBubbling },
		class ConnectedInputState extends React.PureComponent {
			/**
			 * constructor
			 * @param {object} props
			 */
			constructor(props) {
				super(props);

				const initialValue = toString(props.initialValue);

				const { value, error, ...additionalInfo } = format(initialValue, props);

				this.state = {
					hasFocus: false,
					value: initialValue,
					displayValue: value,
					additionalInfo,
					error
				};
			}

			componentWillReceiveProps(nextProps) {
				// Allow new props to update value state _only_ when the field doesn't
				// have focus
				const nextInitialValue = toString(nextProps.initialValue);

				if (
					nextInitialValue !== this.props.initialValue &&
					!this.state.hasFocus
				) {
					const { value, error, ...additionalInfo } = format(
						nextInitialValue,
						nextProps,
						this.state.additionalInfo
					);

					const mergedAdditionalInfo = merge(
						this.state.additionalInfo,
						additionalInfo
					);

					if (
						nextInitialValue !== this.state.value ||
						value !== this.state.displayValue ||
						error !== this.state.error ||
						mergedAdditionalInfo !== this.state.additionalInfo
					) {
						this.setState({
							value: nextInitialValue,
							displayValue: value,
							additionalInfo: mergedAdditionalInfo,
							error
						});
					}
				}
			}

			onValueChange = ({ value, additionalInfo }, format = false) => {
				// todo: format flag is lame
				if (format) {
					const {
						parsedValue,
						formattedValue,
						error,
						additionalInfo: newAdditionalInfo
					} = parseAndFormat(
						value,
						this.props,
						merge(this.state.additionalInfo, additionalInfo)
					);

					this.props.onValueChange({
						value: parsedValue,
						displayValue: formattedValue,
						error
					});

					this.setState({
						value: parsedValue,
						displayValue: formattedValue,
						additionalInfo: merge(this.state.additionalInfo, newAdditionalInfo),
						error
					});
				} else {
					this.props.onStagedValueChange({
						value: value,
						displayValue: value
					});

					this.setState({
						displayValue: value,
						value,
						additionalInfo: merge(this.state.additionalInfo, additionalInfo)
					});
				}
			};

			onFocus = () => {
				this.setState({
					hasFocus: true
				});
			};

			onBlur = event => {
				const {
					parsedValue,
					formattedValue,
					error,
					additionalInfo: newAdditionalInfo
				} = parseAndFormat(
					this.state.value,
					this.props,
					this.state.additionalInfo
				);

				this.setState({
					value: parsedValue,
					displayValue: formattedValue,
					additionalInfo: merge(this.state.additionalInfo, newAdditionalInfo),
					error,
					hasFocus: false
				});

				// value is updated on blur only
				this.props.onValueChange({
					event,
					error,
					value: parsedValue,
					displayValue: formattedValue
				});
			};

			onChange = event => {
				const value = event.target.value;
				this.setState({
					value,
					displayValue: value
				});

				this.props.onStagedValueChange({ event, value });
			};

			/**
			 * render
			 * @return {ReactElement} markup
			 */
			render() {
				const attrs = flow(
					props => omit(props, ['initialValue']),
					props =>
						extend(props, {
							onValueChange: this.onValueChange,
							value: this.state.displayValue,
							error: this.state.error,
							onChange: this.onChange,
							onBlur: this.onBlur,
							onFocus: this.onFocus,
							...this.state.additionalInfo
						})
				)(this.props);

				return <Component {...attrs} />;
			}
		}
	);
};
