import { escapeRegExp, get, map, reduce } from 'lodash';

import { createHttpEffect } from '@servicenow/ui-effect-http';

export const FETCH_MENTIONS = 'FETCH_MENTIONS';
const FETCH_MENTIONS_START = 'FETCH_MENTIONS_START';
const FETCH_MENTIONS_SUCCESS = 'FETCH_MENTIONS_SUCCESS';
const FETCH_MENTIONS_FAILURE = 'FETCH_MENTIONS_FAILURE';

export const BUILT_IN_MENTIONS = {
	users: {
		// Maps the response to something usable. For each entry, the
		// following keys are supported:
		//
		// - id {String} unique id for the item. (required)
		// - value {String} Primary display value for the item (optional)
		// - secondary {String} Secondary display value for the item (optional)
		// - initials {String} Placeholder initials for the avatar (optional)
		// - image {String} Image for the avatar (optional)
		mapResults: data => {
			return map(data, user => {
				const id = user.sys_id;

				const { initials, avatar } = user;

				return {
					id,
					value: user.name,
					secondary: user.email,
					initials,
					image: avatar
				};
			});
		},

		// How to display a new item in the text input.
		// - item {Object} The item to display. Same data as one of the elements
		// returned from `mapResults`.
		displayItem: ({ item, prefix }) => `${prefix}${item.value}`,

		// How to pull initial data from the value string. Should return data in the same
		// format as `mapResults`.
		getItemsFromInitialValue: ({ value, prefix }) => {
			const re = new RegExp(
				`${escapeRegExp(prefix)}\\[([^:]+):([^\\]]+)\\]`,
				'g'
			);
			const items = [];
			let match;
			do {
				match = re.exec(value);
				if (match) {
					items.push({
						id: match[1],
						value: match[2]
					});
				}
			} while (match);

			return items;
		},

		// Maps the raw value (from server) to display format.
		format: ({ value, prefix }) =>
			value.replace(
				new RegExp(`(${escapeRegExp(prefix)})\\[[^:]+:([^\\]]+)\\]`, 'g'),
				'$1$2'
			),

		// Maps the current display value back to server format.
		// - selectedItems {item[]} A list of all items that the user has chosen
		// from the matchlist dropdown.
		parse: ({ value, prefix, selectedItems }) => {
			return reduce(
				selectedItems,
				(result, { value, id }) => {
					return result.replace(
						new RegExp(
							'(^|\\s|\\b)' + escapeRegExp(`${prefix}${value}`) + '(\\s|\\b|$)',
							'g'
						),
						`$1${prefix}[${id}:${value}]$2`
					);
				},
				value
			);
		}
	}
};

export const actionHandlers = {
	[FETCH_MENTIONS]: createHttpEffect(
		'api/now/form/mention/record/:recordTable/:recordId',
		{
			method: 'GET',
			queryParams: ['term'],
			pathParams: ['recordTable', 'recordId'],
			startActionType: FETCH_MENTIONS_START,
			successActionType: FETCH_MENTIONS_SUCCESS,
			errorActionType: FETCH_MENTIONS_FAILURE
		}
	),

	[FETCH_MENTIONS_START]: ({ updateProperties }) => {
		updateProperties({ mentionsLoading: true });
	},

	[FETCH_MENTIONS_SUCCESS]: ({ updateProperties, action }) => {
		const { result } = action.payload;
		updateProperties({ mentionsLoading: false, mentionsResults: result });
	},

	[FETCH_MENTIONS_FAILURE]: ({ updateProperties, action }) => {
		const { result } = action.payload;
		updateProperties({
			mentionsResults: result,
			mentionsLoading: false
		});
	}
};
