import {
	each,
	isArray,
	isEmpty,
	isFunction,
	isObject,
	last,
	reduce
} from 'lodash';

/**
 * Ensures a mention config contains all of the expected properties
 */
export const assertValidMention = mention => {
	if (!isObject(mention)) {
		throw new Error(
			`Invalid mention config: must be an object but got ${typeof mention}`
		);
	}

	// `variables` is the only optional config item
	if (mention.variables && !isFunction(mention.variables)) {
		throw new Error(
			`Invalid type for mention config 'variables'. Expected function but got ${typeof mention.variables}`
		);
	}

	each(['mapResults', 'displayItem', 'format', 'parse'], prop => {
		if (!isFunction(mention[prop])) {
			throw new Error(
				`Invalid type for mention config '${prop}'. Expected function but got ${typeof prop}`
			);
		}
	});
};

/**
 * Compare two search strings to determine whether they will produce
 * related results (one set of results will be a subset of the other).
 */
export const isRelatedSearch = (a, b) =>
	!isEmpty(a) && !isEmpty(b) && (a.startsWith(b) || b.startsWith(a));

export const findSearchSpace = (string, selectionStart) => {
	const valueUntilCursor = string.substring(0, selectionStart);

	// Only look for tokens in the current word
	return last(valueUntilCursor.split(/\s+/));
};

export const shouldShowMentionsSelector = (value, selectionStart) =>
	findSearchSpace(value, selectionStart) === '';

// TODO: Remove later when we can
export const normalizeMentions = mentions => {
	if (!isArray(mentions)) return mentions;

	return reduce(
		mentions,
		(acc, { prefix, type }) => {
			acc[prefix] = type;
			return acc;
		},
		{}
	);
};

export function getCaretPosition(inputEl, { offset = 0 }) {
	/**
	 * Clone textarea/input styles to a dummy div.
	 * Append the text value of the textarea/input
	 * into the dummy div, and then insert a span.
	 *
	 * The position of this span corresponds with
	 * the position of the caret.
	 *
	 * Dummy div is immediately removed after
	 * position is determined.
	 */
	let carPos = inputEl.selectionStart + offset;
	const div = document.createElement('div');
	const span = document.createElement('span');
	const copyStyle = getComputedStyle(inputEl);

	Array.prototype.forEach.call(
		copyStyle,
		prop => (div.style[prop] = copyStyle[prop])
	);

	div.style.position = 'absolute';
	// subtract 1 in the case of a newline
	div.textContent = inputEl.value.substr(0, carPos - 1);
	div.appendChild(span);
	span.textContent = inputEl.value.substr(carPos) || '.';
	document.body.appendChild(div);

	const coords = {
		TOP: span.offsetTop - inputEl.scrollTop,
		LEFT: span.offsetLeft
	};
	document.body.removeChild(div);
	return coords;
}
