import React from 'react';
import MatchList from './matchList/MatchList';
import {
	debounce,
	extend,
	findLastIndex,
	first,
	flow,
	get,
	isEmpty,
	isFunction,
	isString,
	keyBy,
	keys,
	last,
	mapValues,
	noop,
	omit,
	reduce,
	size,
	values,
	toString
} from 'lodash';
import {
	isRelatedSearch,
	findSearchSpace,
	shouldShowMentionsSelector,
	normalizeMentions,
	getCaretPosition,
	assertValidMention
} from './mentionsUtils';
import { FETCH_MENTIONS, BUILT_IN_MENTIONS } from './mentionsActionHandlers';
import { withValueChangeCallbacks } from '@devsnc/sn-controls-common';

const DROPDOWN_MARGIN = 20;

export default Component =>
	withValueChangeCallbacks(
		class Mentions extends React.Component {
			static defaultProps = {
				onValueChange: noop
			};

			constructor(props) {
				super(props);
				this.state = this.getInitialState(props);

				this.runReadQuery = debounce(this.readQuery, props.searchDebounce);
			}

			isLoading = () => get(this.props, 'properties.mentionsLoading', false);

			getInitialState = props => {
				const mentions = mapValues(
					normalizeMentions(props.mentions),
					mention => {
						if (isString(mention)) {
							mention = BUILT_IN_MENTIONS[mention];

							if (!mention) {
								throw new Error(
									`Unknown built-in mentions config '${mention}'. Supported values: '${keys(
										BUILT_IN_MENTIONS
									).join(', ')}'`
								);
							}
						}

						assertValidMention(mention);
						return mention;
					}
				);

				return {
					// map of prefix character (`@`) to mention config
					mentions,

					// tracks items that the user has selected for each mention type
					// {
					//	'@': [ item1, item2 ],
					//	'/': [ item1 ]
					// }
					selectedItems: mapValues(mentions, (mention, prefix) =>
						keyBy(
							mention.getItemsFromInitialValue({
								prefix,
								value: props.value
							}),
							'id'
						)
					),

					// current focused item in the matchlist
					highlightedIndex: 0,

					// search term for the current active query
					searchTerm: '',

					// Any trailing input that exists in the input after the current active
					// mention
					trailingInput: '',

					// data response for the active mention
					matchList: []
				};
			};

			supportsMentions = () => !isEmpty(this.props.mentions);

			scanInputForLastPrefix = (start = this.inputElement.selectionStart) => {
				// Only look for tokens in the current word
				const searchSpace = findSearchSpace(this.inputElement.value, start);

				// only consider search spaces that start with a prefix
				if (this.state.mentions[first(searchSpace)]) {
					const index = findLastIndex(
						searchSpace,
						char => this.state.mentions[char]
					);
					const prefix = this.state.mentions[searchSpace[index]];

					if (prefix) {
						const trailingInput = this.inputElement.value.substring(
							this.inputElement.selectionEnd
						);

						this.showMatchList({
							trailingInput,
							activePrefix: prefix,
							activePrefixPosition: start - searchSpace.length + index
						});

						this.runReadQuery(searchSpace.substring(index + 1));
					}
				}

				return null;
			};

			showMatchList = ({
				activePrefix,
				activePrefixPosition,
				trailingInput
			}) => {
				// Set new position for the popup, to wherever the prefix is (e.g., "@")
				const cursorOffset = getCaretPosition(this.inputElement, {
					offset: 1
				});

				const labelPadding = get(
					this.inputElement,
					'offsetParent.offsetTop',
					0
				);

				this.setState({
					// TODO: This positioning is all hacks
					dropdownLeft: cursorOffset.LEFT,
					dropdownTop: cursorOffset.TOP + labelPadding + DROPDOWN_MARGIN,
					activePrefix,
					activePrefixPosition,
					trailingInput,
					highlightedIndex: 0,
					matchList: []
				});
			};

			componentWillReceiveProps(nextProps) {
				if (nextProps.searchDebounce !== this.props.searchDebounce) {
					this.runReadQuery = debounce(
						this.readQuery,
						nextProps.searchDebounce
					);
				}

				const { activePrefix } = this.state;

				if (!activePrefix) return;

				const nextLoadingState = get(
					nextProps,
					'properties.mentionsLoading',
					false
				);
				const finishedLoading = this.isLoading() && !nextLoadingState;

				if (finishedLoading) {
					const rawData = nextProps.properties.mentionsResults;
					const matchList = activePrefix.mapResults(rawData);
					this.setState({
						matchList,
						highlightedIndex: 0
					});
				}
			}

			displayFormat = value => {
				return reduce(
					this.state.mentions,
					(result, config, prefix) => {
						return config.format({ value: result, prefix });
					},
					toString(value)
				);
			};

			encode = (value, data = this.state.selectedItems) =>
				reduce(
					this.state.mentions,
					(result, config, prefix) => {
						return config.parse({
							value: result,
							prefix,
							selectedItems: values(data[prefix])
						});
					},
					value
				);

			onValueChange = ({ value, data = this.state.selectedItems }) => {
				this.props.onValueChange({
					value: this.encode(value, data)
				});
			};

			innerRef = el => {
				this.inputElement = el;

				if (isFunction(this.props.innerRef)) {
					this.props.innerRef(el);
				}
			};

			onBlur = event => {
				this.hideMatchList();

				if (isFunction(this.props.onBlur)) {
					this.props.onBlur(event);
				}
			};

			onChange = event => {
				const value = event.target.value;

				if (this.state.activePrefix) {
					const searchTerm = value.substring(
						this.state.activePrefixPosition + 1,
						this.inputElement.selectionStart
					);

					this.setState({
						searchTerm,
						isRelatedSearch: isRelatedSearch(this.state.searchTerm, searchTerm)
					});

					if (!isEmpty(searchTerm)) {
						this.runReadQuery(searchTerm);
					}
				}

				if (isEmpty(value)) {
					this.hideMatchList();
				}

				this.props.onValueChange({ event, value: this.encode(value) });
			};

			onKeyDown = event => {
				const { key } = event;
				if (this.state.activePrefix) {
					if (key === 'Escape') {
						this.hideMatchList();
					}
					if (key === 'Enter' && !isEmpty(this.state.matchList)) {
						this.selectItem(this.state.matchList[this.state.highlightedIndex]);
						event.preventDefault();
					}

					if (key === 'ArrowUp') {
						event.preventDefault();
						this.setState({
							highlightedIndex: Math.max(0, this.state.highlightedIndex - 1)
						});
					}

					if (key === 'ArrowDown') {
						event.preventDefault();
						this.setState({
							highlightedIndex: Math.min(
								size(this.state.matchList) - 1,
								this.state.highlightedIndex + 1
							)
						});
					}

					if (key === 'Backspace' || key === 'Delete') {
						// if match list is active & last char is a prefix (char being deleted), hide the match list
						const searchSpace = findSearchSpace(
							this.inputElement.value,
							this.inputElement.selectionStart
						);

						const lastChar = last(searchSpace);

						if (this.state.mentions[lastChar]) {
							this.hideMatchList();
						}
					}
				} else if (key === 'Backspace' || key === 'Delete') {
					// take into account the char that was just deleted from the input when scanning
					this.scanInputForLastPrefix(this.inputElement.selectionStart - 1);
				}

				if (isFunction(this.props.onKeyDown)) {
					this.props.onKeyDown(event);
				}
			};

			onKeyPress = event => {
				const { key } = event;
				const cursorIndex = this.inputElement.selectionStart;
				const isSupportedPrefix = this.state.mentions[key];
				if (
					this.state.activePrefix &&
					// Cursor is now behind the active prefix
					(cursorIndex < this.state.activePrefixPosition ||
						// Space or enter is pressed when there are no matches and we're not trying to load any more.
						((key === ' ' || key === 'Enter') &&
							isEmpty(this.state.matchList) &&
							!this.isLoading()))
				) {
					this.hideMatchList();
				} else if (
					isSupportedPrefix &&
					shouldShowMentionsSelector(
						this.inputElement.value,
						this.inputElement.selectionStart
					)
				) {
					const prefix = this.state.mentions[key];

					// Store any chars after the activePrefix so that we don't stomp over
					// them when injecting a value
					const trailingInput = this.inputElement.value.substring(
						this.inputElement.selectionEnd
					);

					this.showMatchList({
						trailingInput,
						activePrefix: prefix,
						activePrefixPosition: cursorIndex
					});
				}

				if (isFunction(this.props.onKeyPress)) {
					this.props.onKeyPress(event);
				}
			};

			onPaste = event => {
				event.persist();

				// Defer until after contents has been pasted. Needed to get the correct
				// cursor position.
				setTimeout(this.scanInputForLastPrefix, 0);

				if (isFunction(this.props.onPaste)) {
					this.props.onPaste(event);
				}
			};

			selectItem = item => {
				const prefixIndex = this.state.activePrefixPosition;
				const existingChars = this.inputElement.value.substring(0, prefixIndex);
				const prefix = this.inputElement.value[prefixIndex];
				const formatted = this.state.activePrefix.displayItem({
					prefix,
					item
				});

				const selectedItems = {
					...this.state.selectedItems,
					[prefix]: {
						...(this.state.selectedItems[prefix] || {}),
						[item.id]: item
					}
				};

				this.props.onValueChange({
					value: this.encode(
						`${existingChars}${formatted} ${this.state.trailingInput}`,
						selectedItems
					)
				});

				this.setState({
					selectedItems
				});

				this.hideMatchList();
				this.inputElement.focus();
			};

			hoverItem = index => {
				this.setState({
					highlightedIndex: index
				});
			};

			hideMatchList = () => {
				this.setState({
					highlightedIndex: 0,
					activePrefix: null,
					matchList: []
				});
			};

			getInputProps = () => {
				return flow(
					props => omit(props, ['mentions']),
					props =>
						extend(props, {
							value: this.displayFormat(props.value),
							onValueChange: this.onValueChange,
							onChange: this.onChange,
							onKeyPress: this.onKeyPress,
							onKeyDown: this.onKeyDown,
							onPaste: this.onPaste,
							onBlur: this.onBlur,
							innerRef: this.innerRef
						})
				)(this.props);
			};

			getMatchListProps = () => {
				const {
					matchList,
					highlightedIndex,
					dropdownTop,
					dropdownLeft,
					searchTerm,
					isRelatedSearch
				} = this.state;
				const { componentId } = this.props;
				return {
					// Don't show the loading state when the current search results are
					// just a subset (or superset) of the previous results. This feels
					// more like a live filter rather than an entirely separate query.
					isLoading: this.isLoading() && !isRelatedSearch,
					data: matchList,
					highlightedIndex,
					dropdownTop,
					dropdownLeft,
					onItemSelected: this.selectItem,
					componentId,
					onHoverItem: this.hoverItem,
					isNewDropdown: searchTerm.length === 0
				};
			};

			render() {
				if (!this.supportsMentions()) {
					return <Component {...this.props} />;
				}
				const inputProps = this.getInputProps();
				const matchListProps = this.getMatchListProps();

				// TODO: We need to add the MatchList as a target for the FocusWatcher,
				// but Input only adds additional FocusWatcher targets to `dropdown` =/
				//
				// This is super hacky: forcing the MatchList into the dropdown screws
				// up the absolute positioning of the MatchList
				let dropdown = this.props.dropdown;
				const matchList = this.state.activePrefix ? (
					<MatchList {...matchListProps} />
				) : null;

				if (dropdown && matchList) {
					dropdown = (
						<div>
							{dropdown}
							{matchList}
						</div>
					);
				} else if (matchList) {
					dropdown = matchList;
				}

				return (
					<div className="sn-mention-input">
						<Component
							{...inputProps}
							dropdown={dropdown}
							fallbackDropdownBehavior
						/>
					</div>
				);
			}

			readQuery = (inputVal = '') => {
				const { activePrefix } = this.state;

				const hasDoubleSpaceEnding = /\s\s$/.test(inputVal);
				const isValidInput =
					(inputVal.trim() && !hasDoubleSpaceEnding) || inputVal.length === 0;

				if (activePrefix && isValidInput && !hasDoubleSpaceEnding) {
					const { dispatch } = this.props;

					dispatch(
						FETCH_MENTIONS,
						{
							term: inputVal,
							recordTable: this.props.referringTable,
							recordId: this.props.referringRecordId
						},
						{ stopPropagation: true }
					);
				} else if (!isValidInput) {
					this.hideMatchList();
				}
			};
		}
	);
