import React from 'react';
import { startsWith } from 'lodash';
import '@servicenow/now-avatar';

class MatchListItem extends React.PureComponent {
	render() {
		const { highlighted, secondary, value, image } = this.props;
		const className = 'sn-list-group-item' + (highlighted ? ' is-hover' : '');

		const avatarImage = startsWith(image, '/') ? image : `/${image}`;

		return (
			<li
				className={className}
				ref={this._setElement}
				onClick={this._handleClick}
				onMouseOver={this.props.onHoverItem}
			>
				<div className="sn-avatar-container">
					<now-avatar user-name={value} image-src={avatarImage} size="sm" />
				</div>
				<div className="sn-list-group-content sn-list-group-content__wrap has-subtext">
					<span className="sn-list-group-item--title item-value">{value}</span>
					<span className="item-secondary">{secondary}</span>
				</div>
			</li>
		);
	}

	getElementEdges() {
		return {
			topEdge: this.element.offsetTop,
			bottomEdge: this.element.offsetTop + this.element.offsetHeight
		};
	}

	_setElement = el => {
		this.element = el;
	};

	_handleClick = () => {
		this.props.onItemSelected({
			...this.props
		});
	};
}

export default MatchListItem;
