import PropTypes from 'prop-types';
import React from 'react';
import { isFunction, omit } from 'lodash';

import './TextareaResizer';
import autosize from 'autosize';
import debouncedAutosize from './autosize';

export default class Textarea extends React.PureComponent {
	onFocus = e => {
		this.hasFocus = true;

		if (this.props.onFocus) {
			this.props.onFocus(e);
		}
	};

	onBlur = e => {
		this.hasFocus = false;

		if (this.props.onBlur) {
			this.props.onBlur(e);
		}
	};

	componentDidMount() {
		setTimeout(() => {
			if (!this.minHeight && this.textArea) {
				const { height } = window.getComputedStyle(this.textArea);
				this.minHeight = parseInt(height);
			}

			this.autosizer =
				this.props.debounceAutosize > 0
					? debouncedAutosize(this.textArea, this.props.debounceAutosize, false)
					: (autosize(this.textArea),
					  {
							update: () => autosize.update(this.textArea),
							destroy: () => autosize.destroy(this.textArea)
					  });
		});
	}

	componentDidUpdate(prevProps) {
		if (!this.hasFocus && prevProps.value !== this.props.value) {
			this.autosizer && this.autosizer.update();
		}
	}

	componentWillUnmount() {
		this.autosizer && this.autosizer.destroy(this.textArea);
	}

	setRef = node => {
		const { innerRef } = this.props;
		this.textArea = node;
		if (isFunction(innerRef)) innerRef(node);
	};

	render() {
		const { innerRef, resizable, ...props } = this.props;

		return (
			<React.Fragment>
				<textarea
					{...omit(props, 'debounceAutosize')}
					className="sn-textarea"
					onFocus={this.onFocus}
					onBlur={this.onBlur}
					ref={this.setRef}
				/>
				{resizable && (
					<sn-textarea-resizer
						textarea={this.textArea}
						autosize={autosize}
						minHeight={this.minHeight}
					/>
				)}
			</React.Fragment>
		);
	}
}

Textarea.propTypes = {
	showCounter: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
	rows: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	resizable: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
	debounceAutosize: PropTypes.number
};

Textarea.defaultProps = {
	rows: 1,
	resizable: false,
	debounceAutosize: 100
};
