import { isFunction, get, merge } from 'lodash';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';
const NOW_INPUT_PASSWORD_VALUE_SET = 'NOW_INPUT_PASSWORD#VALUE_SET';
const NOW_INPUT_PASSWORD_INVALID_SET = 'NOW_INPUT_PASSWORD#INVALID_SET';
const NOW_TEXTAREA_VALUE_SET = 'NOW_TEXTAREA#VALUE_SET';
const NOW_TEXTAREA_INVALID_SET = 'NOW_TEXTAREA#INVALID_SET';

const passwordChangeEffect = ({ action, dispatch, state }) => {
	const { name, value } = action.payload;
	const onValueChange = adaptCallbackForLegacyAPI(
		state.properties.onValueChange
	);
	if (onValueChange) {
		onValueChange({ name, value });
	} else {
		dispatch('NOW_RECORD_PASSWORD#VALUE_CHANGED', action.payload);
	}
};
const passwordInvalidEffect = payload => {
	const fieldValue = get(payload, 'action.payload.fieldValue', '');
	passwordChangeEffect(
		merge(payload, { action: { payload: { value: fieldValue } } })
	);
};
const textareaChangeEffect = ({ action, dispatch, state }) => {
	const { name, value } = action.payload;
	const onValueChange = adaptCallbackForLegacyAPI(
		state.properties.onValueChange
	);
	if (isFunction(onValueChange)) {
		onValueChange({ name, value });
	} else {
		dispatch('NOW_RECORD_INPUT_CONNECTED#VALUE_CHANGED', action.payload);
	}
};
const textareaInvalidEffect = payload => {
	const fieldValue = get(payload, 'action.payload.fieldValue', '');
	textareaChangeEffect(
		merge(payload, { action: { payload: { value: fieldValue } } })
	);
};
export default {
	actionHandlers: {
		[NOW_INPUT_PASSWORD_VALUE_SET]: {
			effect: passwordChangeEffect,
			stopPropagation: true
		},
		[NOW_INPUT_PASSWORD_INVALID_SET]: {
			effect: passwordInvalidEffect,
			stopPropagation: true
		},
		[NOW_TEXTAREA_VALUE_SET]: {
			effect: textareaChangeEffect,
			stopPropagation: true
		},
		[NOW_TEXTAREA_INVALID_SET]: {
			effect: textareaInvalidEffect,
			stopPropagation: true
		}
	}
};
