import { createCustomElement } from '@servicenow/ui-core';
import React from 'react';
import react from '@servicenow/ui-renderer-react';
import { omit, flow, extend, isFunction } from 'lodash';

import Input from './Input';
import { actionHandlers } from './mentionsActionHandlers';

import withMentions from './withMentions';
import { transformState, ForwardProperties } from '@devsnc/sn-controls-common';

import inputStyles from './journal-input.scss';
import mentionsStyles from './mentions.scss';

const JournalInput = withMentions(
	class extends React.PureComponent {
		onBlur = event => {
			this.props.onValueChange({
				value: this.inputElement.value
			});

			if (isFunction(this.props.onBlur)) {
				this.props.onBlur(event);
			}
		};

		innerRef = node => {
			this.inputElement = node;

			if (isFunction(this.props.innerRef)) {
				this.props.innerRef(node);
			}
		};

		render() {
			const attrs = flow(
				props =>
					omit(props, [
						'initialValue',
						'additionalInfo',
						'fetchDeclarativeActions',
						'dependentField',
						'canWrite',
						'dependentTable',
						'displayValue',
						'declarativeActionsProps',
						'mentionsLoading',
						'mentionsResults',
						'referringTable',
						'referringRecordId',
						'searchDebounce'
					]),
				props =>
					extend(props, {
						onBlur: this.onBlur,
						innerRef: this.innerRef
					})
			)(this.props);

			return <Input {...attrs} componentName="sn-record-input-connected" />;
		}
	}
);

createCustomElement('sn-record-journal-input', {
	renderer: {
		type: react,
		view: ForwardProperties(JournalInput)
	},
	properties: {
		name: { schema: { type: 'string' } },
		initialValue: { schema: { type: 'string' } },
		value: { schema: { type: 'string' } },
		displayValue: { schema: { type: 'string' } },
		label: { schema: { type: 'string' } },
		placeholder: { schema: { type: 'string' } },
		messages: {},
		readonly: { default: false, schema: { type: 'boolean' } },
		required: { default: false, schema: { type: 'boolean' } },
		invalid: { default: false, schema: { type: 'boolean' } },
		description: {},
		onValueChange: {},
		onStagedValueChange: {},
		onFocus: {},
		onBlur: {},
		innerRef: {},
		variant: { schema: { type: 'string' } },
		resizable: { default: false, schema: { type: 'boolean' } },
		mentions: { schema: { anyOf: [{ type: 'object' }, { type: 'array' }] } },
		mentionsLoading: {},
		mentionsResults: {},
		searchDebounce: { schema: { type: 'number' } },
		referringTable: { schema: { type: 'string' } },
		referringRecordId: { schema: { type: 'string' } }
	},
	actionHandlers,
	styles: inputStyles + mentionsStyles,
	transformState
});
