import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import debounce from 'lodash/debounce';
import '@servicenow/now-tooltip';
import '@servicenow/now-icon';
import style from './TextareaResizer.scss';
import { t } from 'sn-translate';

export const TEXTAREA_RESIZE_ELEMENT = 'sn-textarea-resizer';
const DRAGGING_CLASS = 'sn-textarea-resizer-dragging';
const DEBOUNCE_TIME = 5;
const HEIGHT_INCREMENT_PX = 20;
const UP_KEY = 'ArrowUp';
const DOWN_KEY = 'ArrowDown';
const PAGE_UP_KEY = 'PageUp';
const PAGE_DOWN_KEY = 'PageDown';
const PAGE_INCREMENT_PX = 100;
const TOOLTIP_DELAY = 500;
const MAX_HEIGHT_PX = 2000;

const tooltipText = t(
	'Use UP and DOWN arrow keys or drag with mouse to adjust height'
);

const hasClass = (ele, cls) => {
	return !!ele.className.match(new RegExp('(\\s|^)' + cls + '(\\s|$)'));
};

const addClass = (ele, cls) => {
	if (!hasClass(ele, cls))
		ele.className += ele.className !== '' ? ' ' + cls : cls;
};

const removeClass = (ele, cls) => {
	if (hasClass(ele, cls)) {
		const reg = new RegExp('(\\s|^)' + cls + '(\\s|$)');
		ele.className = ele.className.replace(reg, ' ').trim();
		if (ele.className === '') ele.removeAttribute('class');
	}
};

const addStyles = () => {
	const currentStyle = document.getElementById('textarea_resizer_style');
	if (!currentStyle) {
		let styleElem = document.createElement('STYLE');
		styleElem.setAttribute('id', 'textarea_resizer_style');
		styleElem.innerHTML = `
				body.${DRAGGING_CLASS} {
					cursor: ns-resize;
					-webkit-touch-callout: none;
					-webkit-user-select: none;
					-khtml-user-select: none;
					-moz-user-select: none;
					-ms-user-select: none;
					user-select: none;
				}
			`;
		document.head.appendChild(styleElem);
	}
};

const setHeight = debounce((properties, newHeight) => {
	let { textarea, autosize = null } = properties;
	const h = Math.max(
		properties.minHeight || HEIGHT_INCREMENT_PX,
		Math.min(newHeight, MAX_HEIGHT_PX)
	);
	textarea.style.minHeight = h + 'px';
	textarea.style.maxHeight = h + 'px';
	textarea.style.height = h + 'px';
	if (autosize) autosize.update(textarea);
}, DEBOUNCE_TIME);

const openTooltip = debounce((properties, dispatch) => {
	dispatch('PROPERTIES_SET', {
		...properties,
		tooltipOpen: true
	});
}, TOOLTIP_DELAY);

const closeTooltip = (properties, dispatch) => {
	openTooltip.cancel();
	dispatch('PROPERTIES_SET', {
		...properties,
		tooltipOpen: false
	});
};

const getTooltipHandlers = (properties, dispatch) => ({
	onmouseover: () => openTooltip(properties, dispatch),
	onfocus: () => openTooltip(properties, dispatch),
	onblur: () => closeTooltip(properties, dispatch),
	onmouseout: () => closeTooltip(properties, dispatch)
});

const view = (state, dispatch) => {
	const properties = state.properties;
	let dragStartY = -1;
	let originalHeight = -1;

	const handleKeyDown = e => {
		let { textarea } = properties;
		if (!textarea) return;

		const key = e.key;
		if (
			key === DOWN_KEY ||
			key === UP_KEY ||
			key === PAGE_DOWN_KEY ||
			key === PAGE_UP_KEY
		) {
			closeTooltip(properties, dispatch);
			e.stopPropagation();
			e.preventDefault();

			let increment =
				key === DOWN_KEY ? HEIGHT_INCREMENT_PX : 0 - HEIGHT_INCREMENT_PX;

			if (key === PAGE_DOWN_KEY || key === PAGE_UP_KEY)
				increment =
					key === PAGE_DOWN_KEY ? PAGE_INCREMENT_PX : 0 - PAGE_INCREMENT_PX;

			const { maxHeight, height } = window.getComputedStyle(textarea);

			setHeight(
				properties,
				Math.min(parseInt(maxHeight), parseInt(height)) + increment
			);
			openTooltip(properties, dispatch);
		}
	};

	let handleMouseMove;
	const handleMouseUp = () => {
		let { textarea } = properties;
		if (!textarea) return;
		dragStartY = -1;
		removeClass(document.body, DRAGGING_CLASS);
		document.removeEventListener('mousemove', handleMouseMove);
		document.removeEventListener('mouseup', handleMouseUp);
		openTooltip(properties, dispatch);
	};

	handleMouseMove = e => {
		let { textarea } = properties;
		if (!textarea) return;
		closeTooltip(properties, dispatch);
		if (e.buttons > 0) {
			if (dragStartY === -1) {
				dragStartY = e.clientY;
				addClass(document.body, DRAGGING_CLASS);
			}
			setHeight(properties, originalHeight + (e.clientY - dragStartY));
		} else handleMouseUp();
	};

	const handleMouseDown = () => {
		let { textarea } = properties;
		if (!textarea) return;
		closeTooltip(properties, dispatch);
		const { maxHeight, height } = window.getComputedStyle(textarea);
		originalHeight = Math.min(parseInt(maxHeight), parseInt(height));
		document.addEventListener('mousemove', handleMouseMove);
		document.addEventListener('mouseup', handleMouseUp);
	};

	const setTargetRef = ref => {
		dispatch('PROPERTIES_SET', { targetRef: ref });
		addStyles();
	};

	const getCurrentHeight = () => {
		let { textarea } = properties;
		if (!textarea) return 0;
		const { maxHeight, height } = window.getComputedStyle(textarea);
		originalHeight = Math.min(parseInt(maxHeight), parseInt(height));
		return originalHeight;
	};

	const currentHeight = getCurrentHeight();

	return (
		<Fragment>
			<span
				role="slider"
				aria-valuenow={currentHeight}
				aria-valuetext={t('{0} pixels. ', currentHeight) + tooltipText}
				aria-valuemin={properties.minHeight || HEIGHT_INCREMENT_PX}
				aria-valuemax={MAX_HEIGHT_PX}
				className="now-resizer"
				tabIndex="0"
				hook-insert={({ elm }) => setTargetRef(elm)}
				hook-destroy={() => setTargetRef(null)}
				aria-describedby={`${properties.componentId}_tooltip`}
				onmousedown={handleMouseDown}
				onkeydown={handleKeyDown}
				{...getTooltipHandlers(properties, dispatch)}
			>
				<now-icon icon="drag-resize-fill" size="md" />
			</span>
			<now-tooltip
				id={`${properties.componentId}_tooltip`}
				target-ref={properties.targetRef}
				manageOpened={true}
				opened={state.properties.tooltipOpen}
				content={tooltipText}
				delay={TOOLTIP_DELAY}
			/>
		</Fragment>
	);
};

createCustomElement(TEXTAREA_RESIZE_ELEMENT, {
	properties: {
		componentId: {},
		targetRef: { default: null },
		textarea: {},
		minHeight: { default: 20 },
		autosize: {},
		tooltipOpen: { default: false }
	},
	renderer: {
		type: snabbdom,
		view
	},
	styles: style
});
