import React from 'react';
import PropTypes from 'prop-types';
import {
	omit,
	flowRight,
	flow,
	extend,
	isFunction,
	identity,
	toNumber
} from 'lodash';

// Component Imports
import { SNInput, Input } from '@devsnc/sn-record-input';

import withState from './withState';
import inputActionHandlers from './inputActionHandlers';

import withAutocomplete from './withAutocomplete';
import passwordStyle from './password.scss';
import {
	getDeclarativeActionsProps,
	getProperty
} from '@devsnc/sn-controls-common';
import '@devsnc/sn-declarative-actions';
import { transformMessages, isAttrTrue } from '@devsnc/sn-controls-common';
import './mentions';
import '@servicenow/now-input';

/**
 * Adds state management and autocomplete to the basic Input component.
 */
export class ConnectedInput extends React.PureComponent {
	static defaultProps = {
		onValueChange: identity
	};

	isJournal = () => typeof this.props.mentions !== 'undefined';

	onBlur = event => {
		if (!this.isJournal()) {
			this.props.onValueChange({
				value: this.inputElement.value
			});
		}

		if (isFunction(this.props.onBlur)) {
			this.props.onBlur(event);
		}
	};

	innerRef = node => {
		this.inputElement = node;

		if (isFunction(this.props.innerRef)) {
			this.props.innerRef(node);
		}
	};

	renderJournal = () => {
		const attrs = flow(
			props =>
				omit(props, [
					'initialValue',
					'additionalInfo',
					'fetchDeclarativeActions',
					'dependentField',
					'canWrite',
					'dependentTable',
					'displayValue',
					'declarativeActionsProps'
				]),
			props =>
				extend(props, {
					onBlur: this.onBlur,
					innerRef: this.innerRef,
					debounceAutosize: toNumber(
						getProperty('glide.ui.textarea.autosize_debounce', 100)
					),
					searchDebounce: props.debounce
				})
		)(this.props);

		return <sn-record-journal-input {...omit(attrs, ['dispatch'])} />;
	};

	/**
	 * render
	 * @return {ReactElement} markup
	 */
	render() {
		const declarativeActionsProps = getDeclarativeActionsProps(
			this.props.declarativeActionsProps,
			this.props
		);
		const placeholder = this.props.placeholder;
		const readonly = isAttrTrue(this.props.readonly);
		if (this.props.type === 'password') {
			return (
				<now-input-password
					name={this.props.name}
					label={this.props.label}
					placeholder={readonly ? '' : placeholder}
					onchange={this.onChange}
					required={this.props.required}
					readonly={readonly}
					invalid={this.props.invalid}
					manageInvalid
					manageValue
					value={this.props.value}
					messages={transformMessages(this.props.messages)}
				/>
			);
		}
		const attrs = flow(
			props =>
				omit(props, [
					'initialValue',
					'additionalInfo',
					'fetchDeclarativeActions',
					'dependentField',
					'canWrite',
					'dependentTable',
					'displayValue',
					'declarativeActionsProps',
					'mentionsLoading',
					'mentionsResults',
					'referringTable',
					'referringRecordId'
				]),
			props =>
				extend(props, {
					onBlur: this.onBlur,
					innerRef: this.innerRef,
					debounceAutosize: toNumber(
						getProperty('glide.ui.textarea.autosize_debounce', 100)
					)
				})
		)(this.props);

		attrs.disabled = isAttrTrue(attrs.disabled);

		if (this.isJournal()) {
			return this.renderJournal();
		}

		return (
			<Input {...attrs} componentName="sn-record-input-connected">
				{this.props.children}
				{this.props.type !== 'textarea' && (
					<sn-declarative-field-action
						slot="controls"
						model={declarativeActionsProps}
						actions={declarativeActionsProps.declarativeUiActions}
						dispatch={this.props.dispatch}
						formData={this.props.formData}
						fetchDeclarativeActions={
							declarativeActionsProps.fetchDeclarativeActions
						}
					/>
				)}
			</Input>
		);
	}
}

const Base = flowRight(withAutocomplete)(ConnectedInput);

const Component = withState(Base);

export default Component;

Component.actionHandlers = {
	...Base.actionHandlers,
	...inputActionHandlers.actionHandlers
};
Component.style = SNInput.style + ' ' + passwordStyle;
Component.propTypes = {
	...SNInput.propTypes,

	/**
	 * Initial value for the input.
	 */
	initialValue: PropTypes.string,

	/**
	 * Mentions debounce delay before searching
	 */
	debounce: PropTypes.number,

	/**
	 * Disable the field so it can not be used or tabbed to
	 */
	disabled: PropTypes.bool,

	/**
	 * Callback function that is called as the user interacts with the control,
	 * fired as soon as user interaction has begun. It is not guaranteed to be
	 * the final or complete value from the user; use `onValueChange` for that.
	 *
	 * @param {SyntheticEvent} event The react event that triggered the change.
	 * This *can* be null when the value has been changed programatically.
	 *
	 * @param {String} name The name of the component. This will match the value
	 * of the `name` component prop.
	 *
	 * @param {Any} value The new value.
	 */
	onStagedValueChange: PropTypes.func,

	/**
	 * Callback function that is called as the user finishes interacting with
	 * the control.
	 *
	 * @param {SyntheticEvent} event The react event that triggered the change.
	 * This *can* be null when the value has been changed programatically.
	 *
	 * @param {String} name The name of the component. This will match the value
	 * of the `name` component prop.
	 *
	 * @param {Any} value The new value.
	 */
	onValueChange: PropTypes.func,

	/**
	 * When true combines with textbox rendering, the component will display a character
	 * counter showing the amount of characters left.
	 */
	showCounter: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),

	/**
	 * When rendering a textarea, whether or not to show the resize handle.
	 */
	resizable: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),

	/**
	 * When true, the component will automatically suggest possible values to the
	 * user while they interact with the component.
	 */
	autocomplete: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),

	/**
	 * Array of possible values that can be suggested to the user. Requires `autocomplete="true"`.
	 */
	autocompleteOptions: PropTypes.arrayOf(PropTypes.string),

	/**
	 * When true, autocomplete options that match the input in any location will
	 * be suggested.  When false, the autocomplete option must start with the
	 * search string to be considered a match. Requires `autocomplete="true"`
	 */
	autocompleteMatchAnywhere: PropTypes.bool,

	/**
	 * Name of the table the record is listed in
	 */
	referringTable: PropTypes.string,

	/**
	 * record id of the current record where the field is located
	 */
	referringRecordId: PropTypes.string,

	/**
	 * Name of the field
	 */
	fieldName: PropTypes.any,

	/**
	 * sys_id of the current record where the field is located
	 */
	recordSysId: PropTypes.any,

	/**
	 * Each mention will run a GraphQL lookup for a given query each time
	 * a prefix key is typed. Typically used for looking up users when typing the `@` key.
	 */
	mentions: PropTypes.oneOfType([
		PropTypes.object,
		PropTypes.arrayOf(PropTypes.object)
	]),

	fallbackDropdownBehavior: PropTypes.bool,

	/**
	 * Declarative Actions Props
	 */
	declarativeActionsProps: PropTypes.object,

	/**
	 * Determines if a mentions GraphQL is running
	 */
	mentionsLoading: PropTypes.bool,

	/**
	 * Results returned from a mentions GraphQL request
	 */
	mentionsResults: PropTypes.array,

	/**
	 * Determines the type of input to render, text, textarea, or journal
	 */

	type: PropTypes.string,

	dispatches: {
		/**
		 * Dispatched on blur.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_PASSWORD#VALUE_CHANGED': {}
	}
};
