import { debounce } from 'lodash';

const resize = el => {
	// Resets the height to handle shrinking when deleting lines
	el.style.height = '';
	el.style.height = `${el.scrollHeight}px`;
};

const handleScrollToggling = el => {
	const computed = window.getComputedStyle(el, null);

	const actualHeight =
		computed.boxSizing === 'content-box'
			? Math.round(parseFloat(computed.height))
			: el.offsetHeight;
	const styleHeight = Math.round(parseFloat(el.style.height));

	if (actualHeight !== styleHeight) {
		// when content exceeds max-height, enable scroll
		if (computed.overflowY === 'hidden') {
			// Force reflow
			el.offsetWidth;
			el.style.overflowY = 'scroll';
			resize(el);
		}
	} else {
		// Normally keep overflow set to hidden, to avoid flash of scrollbar as the textarea expands.
		if (computed.overflowY !== 'hidden') {
			// Force reflow
			el.offsetWidth;
			el.style.overflowY = 'hidden';
			resize(el);
		}
	}
};

function autosize(el, debounceWait = 100, initialUpdate = true) {
	// we want a leading debounce so that the first resize is invoked
	// to avoid the top line from jumping
	const update = debounce(
		() => {
			resize(el);
			handleScrollToggling(el);
		},
		debounceWait,
		{ leading: true }
	);

	const destroy = () => {
		el.removeEventListener('input', update);
		window.removeEventListener('resize', update);
	};
	if (initialUpdate) update();
	el.addEventListener('input', update);
	window.addEventListener('resize', update);
	el.style.overflowX = 'hidden';
	el.style.wordWrap = 'break-word';

	return {
		update,
		destroy
	};
}

export default autosize;
